@import 'variables.scss';

.wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 0.9375rem;
  height: 3.125rem;
  padding: 0.5rem 0.625rem;
  padding-right: 1.25rem;
  margin-bottom: 0.3125rem;
  border-radius: 0.75rem;
  background-color: $c-mirage;

  .info {
    display: flex;
    align-items: center;

    .iconWrapper {
      width: 2.125rem;
      height: 2.125rem;
      background-color: rgba($c-titan-white, 0.05);
      border-radius: $border-radius;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 0.625rem;
    }

    .failedIcon {
      border-radius: 0;
      width: 1.375rem;
    }

    .innerImage {
      border-radius: $border-radius;
    }

    .name {
      font-size: 0.875rem;
      font-weight: 400;
      margin: 0;

      span {
        color: $c-scampi;
      }
    }
  }
}
